import { Fragment, useState, useEffect } from "react"

import classes from "./landingPage.module.css"
import Banner from "../components/banner"
import HiThere from "../components/hiThere"
import Mansory from "../components/mansory"
import Technologies from "../components/technologies"
import Services from "../components/services"
import Footer from "../components/core/footer"
import SpinnerSmall from "../components/UI/spinner-small"
import { apiRootURL } from "../apiConfigs"

const LandingPage = () => {

    const [ loading, setLoading ] = useState(true)
    const [ technologies, setTechnologies ] = useState([])
    const [ error, setError ] = useState(false)
    useEffect(() => {
        fetch(apiRootURL + "development/technologies").then(res=>{
            return res.json()
        }).then(jsonRes=>{
            const fetchedTechs = [...jsonRes]
            return setTechnologies((()=>{ return [...fetchedTechs]}))

        }).then(()=>{
            setLoading(false)
        }).catch(err=>{
            setError(true)
            setLoading(false)
        })
    },[])


    return (
        <Fragment>
            <Banner />
            <div className={classes["grey-container"]}>
                <div className={classes["size-container"]}>
                    <HiThere title={"Hi there!"}  fontClass={"font-big"} description={"I'm an innovative software engineer with several years of experience managing all aspects of the development process for small to medium-sized companies."} />
                    <Mansory limit={4} />
                    {loading? <SpinnerSmall /> :  <Technologies title={"Technologies I Use"} technologies={technologies} error={error} />}
                </div>
            </div>
            <Services />
            <Footer text={"Let's get in touch!"} to="/contact-me"/>
        </Fragment>
    )
}

export default LandingPage;