import { Fragment, useEffect, useState } from "react";
import { Link } from "react-router-dom";

import classes from "../pages/projects.module.css";
import SpinnerSmall from "./UI/spinner-small";
import { apiRootURL } from "../apiConfigs";
import ProjectBanner from "./projectBanner";

const Mansory = (props) => {
  const [loading, setLoading] = useState(true);
  const [projects, setProjects] = useState([]);
  const [error, setError] = useState(false);
  useEffect(() => {
    fetch(apiRootURL+"development/projects")
      .then((res) => {
        return res.json();
      })
      .then((jsonRes) => {
        const fetchedProjects = [...jsonRes.projects].slice(0, props.limit);
        return setProjects(() => {
          return [...fetchedProjects];
        });
      })
      .then(() => {
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }, [props.limit]);

  let content = loading ? (
    <SpinnerSmall />
  ) : (
    <div className="projects-container">
      <div className={classes["project-list"]}>
        {projects.map((project) => {
          return (
            <ProjectBanner key={project._id} project={project}/>
          );
        })}
      </div>
      <div className={classes["view-all"]}>
        <Link className="link-item" to="/projects">
          View all projects
        </Link>
      </div>
    </div>
  );

  if (error) {
    content = <div className={classes["error-small"]}>something went wrong</div>;
  }

  return (
    <Fragment>
      <div className={classes["projects-component"]}>{content}</div>
    </Fragment>
  );
};

export default Mansory;
